import { useApp, useTick } from "@pixi/react";
import { useContext, useEffect, useMemo, useRef, useState } from "react"
import { IOrientationType } from "../util/constant";
import { SlotContext } from "../model/context";
import { GameContext } from "../AppContext";
import { action_setOrientation } from "../model/setting";
import { SoundService } from "./soundService";
import { EventEmitter } from 'events'
import { IPointData } from 'pixi.js'

const htmlEl = document.querySelector('html');
export const I_RESIZE = 'resize';



export const LayoutServiceFC = () => {
    const app = useApp();

    const { current } = useRef<{windowSize: [number, number]}>({windowSize: [0, 0]}) 
    const { state, dispatch } = useContext(GameContext);
    const slotContext = SlotContext.Obj;

    const appEl = useMemo(() => {
        return document.getElementById('App');
    }, []);

    const [hidden, visibilityChange] = useMemo(() => {
        if (typeof document.hidden !== "undefined") {
            return ['hidden', 'visibilitychange']
        } else if (typeof document['msHidden'] !== "undefined") {
            return ['msHidden', 'msvisibilitychange']
        } else if (typeof document['webkitHidden'] !== "undefined") {
            return ['webkitHidden', 'webkitvisibilitychange']
        } else {
            console.error('visibilitychange events not supported');
        }
        return ['hidden', 'visibilitychange'];
    },[]);

    //check common/src/components/scene.tsx
    const resize = () => {
        SlotContext.Obj.updateOrientation();
        const orientation = slotContext.orientation;
        let { displayArea, safeArea } = slotContext.layoutOptions;
        const [displayLeft, displayTop, displayAreaWidth, displayAreaHeight] = displayArea, displayAreaRatio = displayAreaHeight / displayAreaWidth;
        safeArea = safeArea ? safeArea : [0,0,displayAreaWidth, displayAreaHeight];
        const [safeAreaX, safeAreaY, safeAreaWidth, safeAreaHeight] = safeArea, safeAreaRatio = safeAreaHeight / safeAreaWidth;

        const windowWidth = document.documentElement.clientWidth, windowHeight = document.documentElement.clientHeight, windowRatio = windowHeight / windowWidth;
        let rendererWidth, rendererHeight, top, left, scale, fontscale, appWidth, appHeight;
        //计算出要render的大小。然后修改left，left进行移动
        if (orientation == 'l') {
            if (windowRatio <= safeAreaRatio) {
                rendererHeight = windowHeight * (displayAreaHeight / safeAreaHeight);
                rendererWidth = rendererHeight / displayAreaRatio;

                rendererHeight = Math.min(displayAreaHeight, rendererHeight);
                rendererWidth = Math.min(displayAreaWidth, rendererWidth);
                appHeight = windowHeight;
                appWidth = rendererWidth;
            } else {//if (windowRatio <= displayAreaWidth) {
                if (windowWidth <= safeAreaWidth) {
                    rendererWidth = windowWidth * (displayAreaWidth / safeAreaWidth);
                } else if (windowWidth <= displayAreaWidth) {
                    rendererWidth = displayAreaWidth;
                } else {
                    rendererWidth = windowWidth;
                }
                rendererHeight = rendererWidth * displayAreaRatio;

                rendererHeight = Math.min(displayAreaHeight, rendererHeight);
                rendererWidth = Math.min(displayAreaWidth, rendererWidth);
                appHeight = rendererHeight;
                appWidth = windowWidth;
            }
        } else {
            if (windowRatio >= safeAreaRatio) {
                rendererWidth = windowWidth * (displayAreaWidth / safeAreaWidth);
                rendererHeight = rendererWidth * displayAreaRatio;

                rendererHeight = Math.min(displayAreaHeight, rendererHeight);
                rendererWidth = Math.min(displayAreaWidth, rendererWidth);

                appWidth = windowWidth;
                appHeight = rendererHeight;
            } else {
                if (windowHeight <= safeAreaHeight) {
                    rendererHeight = windowHeight * (displayAreaHeight / safeAreaHeight);
                } else if (windowHeight <= displayAreaHeight) {
                    rendererHeight = displayAreaHeight;
                } else {
                    rendererHeight = windowHeight;
                }
                rendererWidth = rendererHeight / displayAreaRatio;

                rendererHeight = Math.min(displayAreaHeight, rendererHeight);
                rendererWidth = Math.min(displayAreaWidth, rendererWidth);

                appWidth = rendererWidth;
                appHeight = windowHeight;
            }
        }

        scale = rendererWidth / displayAreaWidth;
        fontscale = rendererWidth / displayAreaWidth;
        left = (windowWidth - rendererWidth) / 2;
        top = (windowHeight - rendererHeight) / 2;
        const appLeft = Math.round(Math.min(left, 0)); //const appLeft = Math.round(Math.min(left, 0) * precentX);
        const appTop = Math.round(Math.min(top, 0)) //const appTop = Math.round(Math.min(top, 0) * precentY)

        app.stage.scale.set(scale);
        app.renderer.resize(rendererWidth, rendererHeight);
        
        const _appTop = Math.max(appTop/scale, displayTop - safeAreaY) * scale;
        const _appLeft = Math.max(appLeft/scale, -(safeAreaX - displayLeft)) * scale;

        (app.view.style as any).top = `${_appTop}px`;
        (app.view.style as any).left = `${_appLeft}px`;

        const _width = Math.round(Math.min(windowWidth, appWidth, rendererWidth));
        const _height = Math.round(Math.min(windowHeight, appHeight, rendererHeight));
        const _top = Math.round(Math.max(0, top));
        const _left = Math.round(Math.max(0, left));
        
        appEl.style.width = `${_width}px`;
        appEl.style.height = `${_height}px`;
        appEl.style.top = `${_top}px`;
        appEl.style.left = `${_left}px`;

        let size = 0;
        if (orientation == 'l') {
            size = Math.max(12, 20 * (document.documentElement.clientWidth - _left * 2) / 960);
        } else {
            size = Math.max(12, 20 * document.documentElement.clientWidth / 540);
        }
        htmlEl.style.fontSize = `${size}px`;
        
        if (state.setting.orientation != orientation) {
            dispatch(action_setOrientation(orientation));
        } 
        //notice 
        SlotContext.Obj.updateViewport(appLeft/scale, appTop/scale, Math.round(_width/scale), Math.round(_height/scale));
        slotContext.notice.emit(I_RESIZE);
    }

    function onVisibilityChange() {
        if (document[hidden]) {
            SoundService.Obj.pauses(true);
            app.ticker.stop(); //must stop, for spine animation
        } else {
            SoundService.Obj.pauses(false);
            app.ticker.start();
        }
    }   

    useEffect(() => {
        document.addEventListener(visibilityChange, onVisibilityChange);
        return () => {
            document.removeEventListener(visibilityChange, onVisibilityChange);
        }
    })

    useTick((delta: number, ticker) => {
        if (current.windowSize[0] != document.documentElement.clientWidth || current.windowSize[1] != document.documentElement.clientHeight) {
            resize();
            current.windowSize = [document.documentElement.clientWidth, document.documentElement.clientHeight];
        }
    }, true);

    return <></>
}