import { Int } from "./math";

export function mapWith<T = any>(number: number, cb: (i: number) => T): T[] {
    let res = [];
    for (let i = 0; i < number; i++) {
        res.push(cb(i));
    }
    return res;
}

export function atLast<T>(arr: T[]): T {
    return arr[arr.length - 1];
}

export function randomInArr<T>(arr: T[]): T {
    return arr[Int.Random(arr.length)]
}

export function randomInArrWith<T>(arr: T[], length: number): T[] {
    const result = [];
    for (var i = 0; i <length; i++) {
        var ran = Math.floor(Math.random() * (arr.length - i));
        result.push(arr[ran]);
        arr[ran] = arr[arr.length - i - 1];
    }
    return result;
}