import Decimal from "decimal.js";
import { ErrorModel } from "../error";

export class BaseSpinModel extends ErrorModel {
    credit: Decimal;
    cost: Decimal;
    payout: Decimal;
    balance: Decimal;

    constructor(data) {
        super(data);
        if (this.code) return;
        this.credit = new Decimal(data.credit || 0);
        this.cost = new Decimal(data.cost || 0);
        this.payout = new Decimal(data.payout || 0);
        this.balance = new Decimal(data.balance || 0);
    }
}