import { forwardRef, useEffect, useImperativeHandle, useMemo, useRef, useState } from "react";
import { Tween as TWEEN, remove as RemoveTween } from "@tweenjs/tween.js";
import { Layer } from "./layer";
import { KComponentsOptions } from "./component";
import { SlotContext } from "../model/context";

export interface KTweenActions {
    repeated: number,
    repeat: number
}

export interface KTweenOptions {
    from?: { [key: string]: number },
    to?: { [key: string]: number },
    duration?: number,
    repeat?: number,
    repeatDelay?: number,
    yoyo?: boolean,
    delay?: number
    easing?: (k: number) => number,
    onStart?: (e) => void,
    onUpdate?: (e, elapsed?: number) => void,
    onComplete?: (e) => void;
    onRepeat?: (e) => void;
}

interface KTweenComponentOptions extends KTweenOptions, KComponentsOptions {
    children: (e, elapsed?: boolean) => any;
    playing?: boolean;
    dep?: any[]
}

export const Tween = forwardRef<KTweenActions, KTweenComponentOptions>(({ dep = [], children, id, from = {}, to = {}, playing = false, duration, yoyo, repeat, repeatDelay, delay = 0, easing = k => k, onStart, onComplete, onRepeat, onUpdate }, ref) => {
    const [forceUpdate, setForceUpdate] = useState(false);
    const { current } = useRef<{ tween: TWEEN<any> }>({ tween: null });

    function destoryTween() {
        current.tween?.stop();
        current.tween = null;
    }

    useMemo(() => {
        destoryTween();
        if (playing) {
            current.tween = new TWEEN({ ...from })
                .to({ ...to }, duration)
                .repeat(repeat)
                .repeatDelay(repeatDelay)
                .delay(delay)
                .yoyo(yoyo)
                .start(SlotContext.Obj.tweenTime);
        }
    }, [playing, ...dep]);

    useMemo(() => {
        current.tween?.repeat(repeat);
    }, [repeat]);

    current.tween?.easing(easing)
        .onStart(onStart)
        .onUpdate((props, elapsed) => {
            onUpdate && onUpdate(props, elapsed);
            setForceUpdate(!forceUpdate);
        })
        .onRepeat(onRepeat)
        .onComplete(onComplete);

    useImperativeHandle(ref, () => {
        return {
            get repeated() {
                return current.tween['_initialRepeat'] - current.tween['_repeat'];
            },
            get repeat() {
                return current.tween['_repeat'];
            }
        }
    });

    useEffect(() => {
        () => {
            destoryTween();
        }
    }, [playing, ...dep]);

    return children(current.tween ? current.tween['_object'] : from, current.tween?.isPlaying() || false)
})