import { Container, ParticleContainer } from "@pixi/react";
import { Rectangle } from "pixi.js";
import { ComponentProps, forwardRef, useEffect, useMemo, useState } from "react";
import { KComponentsOptions } from "./component";
import { useOrientationChange } from "./customhook";

export interface KLayerOptions extends ComponentProps<typeof Container>, KComponentsOptions {
    hitFrame?: [number, number, number, number];
    children?: any
}

export const Layer = forwardRef(({hitFrame, children, ...props}: KLayerOptions, ref) => {
    const hitArea = useMemo(() => {
        if (hitFrame) {
            return new Rectangle(...hitFrame);
        }
        return null
    }, []);

    return <Container hitArea={hitArea} {...props} ref={ref}>
        {children}
    </Container>
});

export const Orientation = ({children}) => {
    return useOrientationChange(children);
}
