export function getPaylineMap(map: {readonly [key: string]: number[]}): {readonly [key: string]: [number, number][]} {
    let res = {};
    for (let key in map) {
        let item = map[key];
        res[key] = item.map((row, column) => {
            return [column, row];
        });
    }
    return res;
}

//
export function getPaylinePathMap(map: {readonly [key: string]: number[]}): {readonly [key: string]: [number, number][]} {
    let res = {};
    for (let id in map) {
        let symbolIndexs = map[id], length = symbolIndexs.length, path = [];
        for (let i = 0; i < length; i++) {
            let index = symbolIndexs[i];
            path.push([i + 0.5, index + 0.5]);
        }
        let firstIndex = symbolIndexs[0];
        path.unshift([0, firstIndex + 0.5]);
        let lastIndex = symbolIndexs[length - 1];
        path.push([length, lastIndex + 0.5]);
        res[id] = path;
    }   
    return res;
}


//for wuxia clone
export function getPaylinePathMapV2(map: {readonly [key: string]: number[]}): {readonly [key: string]: [number, number][]} {
    const res =  getPaylinePathMap(map);
    for (let key in res) {
        const items = res[key];
        for (let item of items) {
            item[1] = item[1] - 0.5 * Math.floor(Math.min(item[0], 4));
        }
    }
    return res;
}
