import { LineBets } from '../util/parser/lineBets/base';
import {
    ACTION_STOP_AUTO_SPIN, ACTION_DO_AUTO_SPIN, ACTION_SPIN, ACTION_SPIN_STOP,
    ACTION_SPIN_STOPPING, KReducerActionOptions, ACTION_SET_BET_AMOUNT, ACTION_SET_AUTO_SPIN_DETAIL, ACTION_SET_SPIN_SPEED, ACTION_SET_BET_LEVEL, ACTION_LOADED, ACTION_LINEBETS_LOADED
} from "./actionTypes";
import {I_NO_LIMIT_SYMBOL, I_NO_LIMIT_TYPE} from '../util/constant';
import Decimal from "decimal.js";

export type KSpinStatus = 'idle' | 'spin' | 'stopping';
export type KSpinSpeedModel = 'normal' | 'fast';

export interface KSpinReducerOptions<SPIN> {
    spin: {
        status: KSpinStatus;  //idle -> 点击spin后 spin -> 接收到消息后 stopping -> spin动画自然结束或强制结束 stop -> win lin -> idle
        spinModel?: SPIN;  //spin result
        lineBetsModel?: LineBets,
        betAmount: Decimal,
        betLevel: Decimal,
        speedModel: KSpinSpeedModel,
        autoSpinCount: number | I_NO_LIMIT_TYPE;
        stopAutoSpinLimit: {
            totalBetTimes: Decimal | I_NO_LIMIT_TYPE,
            signleWin: Decimal,
            balanceTo: Decimal
        }
    }
}

export const spin = (state: any = {}, action: KReducerActionOptions) => {
    // console.log(action)
    switch (action.type) {
        case ACTION_SPIN:
            let status = state.status;
            if (state.status == 'idle') {
                status = 'spin'
            } else if (state.status == 'stopping') {
                status = 'idle';
            } else {
            }
            return {
                ...state,
                status: status
            }
        case ACTION_LOADED:
            return {
                ...state,
                spinModel: action.value
            }
        case ACTION_SPIN_STOPPING:
            return {
                ...state,
                status: 'stopping',
                spinModel: action.value,
            }
        case ACTION_SPIN_STOP:
            return {
                ...state,
                status: 'idle'
            }
        case ACTION_SET_BET_AMOUNT: {
            return {
                ...state,
                betAmount: action.value
            }
        }
        case ACTION_SET_BET_LEVEL: {
            return {
                ...state,
                betLevel: action.value
            }
        }
        case ACTION_SET_AUTO_SPIN_DETAIL: {
            return {
                ...state,
                autoSpinCount: action.value.autoSpinCount,
                stopAutoSpinLimit: action.value.stopAutoSpinLimit
            }
        }
        case ACTION_DO_AUTO_SPIN: {
            let autoSpinCount = state.autoSpinCount;
            if (autoSpinCount != I_NO_LIMIT_SYMBOL) {
                --autoSpinCount;
            }
            return {
                ...state,
                status: 'spin',
                autoSpinCount: autoSpinCount
            }
        }
        case ACTION_STOP_AUTO_SPIN: {
            return {
                ...state,
                autoSpinCount: 0
            }
        }
        case ACTION_SET_SPIN_SPEED: {
            return {
                ...state,
                speedModel: action.value
            }
        }
        case ACTION_LINEBETS_LOADED: {
            return {
                ...state,
                lineBetsModel: action.value
            }
        }
        default:
            return state;
    }
}

export const spinInitialState: KSpinReducerOptions<any> = {
    spin: {
        status: 'idle',
        autoSpinCount: 0,
        lineBetsModel: null,
        speedModel: 'normal',
        stopAutoSpinLimit: {
            totalBetTimes: new Decimal(0),
            signleWin: new Decimal(0),
            balanceTo: new Decimal(0)
        }
    }
}

export function action_spin(): KReducerActionOptions {
    return {
        type: ACTION_SPIN
    }
}

export function action_loaded(spinModel: any): KReducerActionOptions {
    return {
        type: ACTION_LOADED,
        value: spinModel
    }
}

export function action_spinStopping(spinModel: any): KReducerActionOptions {
    return {
        type: ACTION_SPIN_STOPPING,
        value: spinModel
    }
}

export function action_spinStop(): KReducerActionOptions {
    return {
        type: ACTION_SPIN_STOP
    }
}

export function action_setBetAmount(value: Decimal.Value): KReducerActionOptions {
    return {
        type: ACTION_SET_BET_AMOUNT,
        value: new Decimal(value)
    }
}

export function action_setbetLevel(value: Decimal.Value): KReducerActionOptions {
    return {
        type: ACTION_SET_BET_LEVEL,
        value: new Decimal(value)
    }
}

export function action_setAutoSpinDetail(autoSpinCount: number | I_NO_LIMIT_TYPE, totalBetTimes: number | I_NO_LIMIT_TYPE, signleWin: number, balanceTo: number): KReducerActionOptions {
    return {
        type: ACTION_SET_AUTO_SPIN_DETAIL,
        value: {
            autoSpinCount: autoSpinCount,
            stopAutoSpinLimit: {
                totalBetTimes: totalBetTimes == I_NO_LIMIT_SYMBOL ? I_NO_LIMIT_SYMBOL : new Decimal(totalBetTimes),
                signleWin: new Decimal(signleWin),
                balanceTo: new Decimal(balanceTo),
            }
        }
    }
}

export function action_doAutoSpin(): KReducerActionOptions {
    return {
        type: ACTION_DO_AUTO_SPIN
    }
}

export function action_stopAutoSpin(): KReducerActionOptions {
    return {
        type: ACTION_STOP_AUTO_SPIN
    }
}

export function action_setSpinSpeed(e: KSpinSpeedModel): KReducerActionOptions{
    return {
        type: ACTION_SET_SPIN_SPEED,
        value: e
    }
}

export function action_lineBets_loaded(e: LineBets): KReducerActionOptions {
    return {
        type: ACTION_LINEBETS_LOADED,
        value: e
    }
}