import { useContext, useEffect } from "react";
import { GameContext } from "../AppContext";
import { action_error_detail } from "../model/setting";
import { isNumber } from "../util/lang";


export enum ErrorCodesInservice {
    NotEnoughBalance = 1000,
    UserNotFound = 1001,
    ErrorReadGameState = 1002,
    InvalidCredit = 1003,
    ConcurrentSpin = 1004,
    NoGameState = 1005,
    InvalidGameState = 1006,
    ExpiredState = 1007,
    InternalFailure = 1008,
    ExternalFailure = 1009
}

    
export const ErrorService = () => {
    const { state, dispatch } = useContext(GameContext);
    const { code } = state.setting.error;

    useEffect(() => {
        if (code) {
            if (code === '000') {
                dispatch(action_error_detail(code, true, false));
            } else if (code === '001') {
                dispatch(action_error_detail(code, true, true));
            } else {
                if (code == ErrorCodesInservice.NotEnoughBalance) {
                    dispatch(action_error_detail(code, true, true));
                } else if (code == ErrorCodesInservice.NoGameState) {
                    dispatch(action_error_detail('', false, true));
                }  else {
                    dispatch(action_error_detail(code, true, false));
                } 
            }
        }
    }, [code]);

    return <></>
}
