import { useContext } from 'react';
import { GameContext } from '../AppContext';
import './loading.scss';

export function ScenePreloadLoading() {
    const {state} = useContext(GameContext);
    const {scenes} = state.sceneManager;
    const isPreloadPage = scenes[scenes.length - 1] == 'preload';
    return (
        <div className={`page-mod ${isPreloadPage ? 'db' : 'dn'}`}>
            <div className="scene-preload-mod">
                {/* <div className="progress"></div> */}
                <div className="loading-animation-default-mod">
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                </div>
            </div>
        </div>
    )
}

export function SceneLoginLoading() {
    const {state} = useContext(GameContext);
    const {scenes, assetsLoadingProgress} = state.sceneManager;

    const isLoginPage = scenes[scenes.length - 1] == 'load';
    return (
        <div className={`page-mod ${isLoginPage ? 'db' : 'dn'}`}>
            <div className="scene-login-loading-mod">
                <div className="progress">{`${assetsLoadingProgress}%`}</div>
                <div className="loading-animation-default-mod">
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                </div>
            </div>
        </div>
    )
}