import { useContext, useMemo } from "react";
import { GameContext } from "../AppContext";

// function showBlurBackground() {
//     const el = document.getElementById('blur-bg');
//     el.style.setProperty('display', 'block', 'important');
// }

function showGame() {
    const el = document.getElementById('context');
    el.style.setProperty('opacity', '1');
    el.className = 'context-fadein';

    const logoEl = document.getElementById('logo');
    document.body.removeChild(logoEl);
}

export const LaunchService = () => {
    const { state } = useContext(GameContext);
    const { scenes } = state.sceneManager;

    const isLoadPage = scenes[scenes.length - 1] == 'load';

    useMemo(() => {
        if (isLoadPage) {
            showGame();
        }
    }, [isLoadPage]);

    return <></>
}