import { ISceneIdType } from '../util/constant';
import { ACTION_SCENE_PUSH, ACTION_SCENE_POP, ACTION_SCENE_REPLACE, ACTION_SCENE_TO_ROOT, KReducerActionOptions, ACTION_SCENE_ASSETS_LOADING_PROGRESS_CHANGED } from './actionTypes';

export interface KReducerSceneManagerOptions {
    sceneManager: {
        scenes: [ISceneIdType?],
        assetsLoadingProgress: number
    }
}

export const sceneManager = (state: any = {}, action: KReducerActionOptions) => {
    let scenes = [].concat(state.scenes);
    switch (action.type) {
        case ACTION_SCENE_PUSH: 
            scenes.push(action.value);
            return {
                ...state,
                scenes: scenes
            }
        case ACTION_SCENE_POP:
            scenes.pop();
            return {
                ...state,
                scenes: scenes
            }
        case ACTION_SCENE_REPLACE: 
            scenes.pop();
            scenes.push(action.value);
            return {
                ...state,
                scenes: scenes
            }
        case ACTION_SCENE_TO_ROOT:
            return {
                ...state
            }
        case ACTION_SCENE_ASSETS_LOADING_PROGRESS_CHANGED:
            return {
                ...state,
                assetsLoadingProgress: action.value
            }
        default:
            return state
    }
}

export const sceneManagerInitialState: KReducerSceneManagerOptions = {
    sceneManager: {
        scenes: ['preload'],
        assetsLoadingProgress: 0
    }
}

export function action_scenePush(sceneId: ISceneIdType): KReducerActionOptions {
    return {
        type: ACTION_SCENE_PUSH,
        value: sceneId
    }
}

export function action_scenePop(): KReducerActionOptions {
    return {
        type: ACTION_SCENE_POP
    }
}

export function action_sceneReplace(sceneId: string): KReducerActionOptions {
    return {
        type: ACTION_SCENE_REPLACE,
        value: sceneId
    }
}

export function action_sceneToRoot(): KReducerActionOptions {
    return {type: ACTION_SCENE_TO_ROOT}
}

export function action_sceneAssetsLoadingProgressChanged(progress: number): KReducerActionOptions {
    return {
        type: ACTION_SCENE_ASSETS_LOADING_PROGRESS_CHANGED,
        value: progress
    }
}

