import { useMemo } from "react";
import { ISceneIdType } from "../util/constant";
import { Layer, KLayerOptions, Orientation } from "./layer";
import { SlotContext } from "../model/context";

export interface KSceneOptions extends KLayerOptions {
    id?: ISceneIdType;
};

export const Scene = (props: KSceneOptions) => {
    const { children, ...newProps } = props;
    const { l, p } = SlotContext.Obj.configOptions.layoutMap;

    const orientationEnable = useMemo<Boolean>(() => {
        return !!(l && p);
    }, [])

    const [lx, ly, px, py] = useMemo(() => {
        if (orientationEnable) {
            return [-l.displayArea[0], -l.displayArea[1], -p.displayArea[0], -p.displayArea[1]];
        } else {
            const t = l ?? p;
            return [-t.displayArea[0], -t.displayArea[1], 0, 0]
        }
    }, []);

    return (
        <Layer {...newProps}>
            {
                orientationEnable ?
                    <Orientation>
                        <Layer l-x={lx} l-y={ly} p-x={px} p-y={py}>
                            {children}
                        </Layer>
                    </Orientation>
                    : <Layer x={lx} y={ly}>
                        {children}
                    </Layer>
            }
        </Layer>
    )
};