import { Scene } from './scene';
import { loadAssets, KRrsourceOptions, getLang } from '../util/assetsLoad';
import { KSceneOptions } from './scene';
import { useContext, useEffect, useMemo, useRef, useState } from 'react';
import { ISceneIdType } from '../util/constant';
import { action_sceneAssetsLoadingProgressChanged, action_sceneReplace } from '../model/sceneManager';
import { SlotContext } from '../model/context';
import { GameContext, GameContextOptions } from '../AppContext';
import { ProgressBarTweenLayer } from './progress';
import { useFrame } from './customhook';
import { R_Message } from '../assets';

export interface KLoadSceneOptions extends KSceneOptions {
    assets?: readonly KRrsourceOptions[],
    next?: ISceneIdType
};

export const LoadScene = ({ assets, next, ...props }: KLoadSceneOptions) => {
    const assetVersion = SlotContext.Obj.configOptions.assetVersion!;
    const {state, dispatch} = useContext(GameContext) as GameContextOptions<any>;
    const { current } = useRef<{loaded: boolean, loggingIn: boolean}>({loaded: false, loggingIn: false});

    useEffect(() => {
        dispatch(action_sceneAssetsLoadingProgressChanged(0));
        loadAssets(assets, assetVersion, (e) => {
            dispatch(action_sceneAssetsLoadingProgressChanged(Math.floor(e * 100)));
        }).then(() => {
            current.loaded = true;
            dispatch(action_sceneReplace(next));
        });
        return () => {
            const title = getLang(R_Message, 'gametitle');
            document.title = title;
        }
    }, []);

    return <Scene {...props}/>
}

interface LoadSceneV2Options extends KSceneOptions {
    children: (progress: number,  text: string) => any,
    assets?: readonly KRrsourceOptions[],
    next?: ISceneIdType
}

export const LoadSceneV2 = ({ children, assets, next, ...props }: LoadSceneV2Options) => {
    const assetVersion = SlotContext.Obj.configOptions.assetVersion!;
    const {state, dispatch} = useContext(GameContext) as GameContextOptions<any>;
    const { current } = useRef<{loaded: boolean, loggingIn: boolean}>({loaded: false, loggingIn: false}); 
    const { loggingIn } = state.user;
    const [ progress, setPeogress ] = useState(0);
    const [ textIndex, setTextIndex ] = useState(0);

    function turnNext() {
        if (current.loggingIn && current.loaded) {
            if (next) dispatch(action_sceneReplace(next));
        }
    }

    function onProgressComplete(e: number) {
        if (progress == 100) {
            current.loaded = true;
            turnNext();
        }
    }

    useEffect(() => {
        if (loggingIn) {
            current.loggingIn = true;
        }
        turnNext();
    }, [loggingIn])

    const [authText, loadingText] = useMemo(() => {
        return [getLang(R_Message, 'loggingin'), getLang(R_Message, 'loading')];
    }, []);

    const text = useMemo(() => {
        if (progress == 100) {
            return [`${authText}.`, `${authText}..`, `${authText}...`][textIndex];
        } else {
            return [`${loadingText}.`, `${loadingText}..`, `${loadingText}...`][textIndex];
        }
    }, [textIndex, progress])

    useEffect(() => {
        dispatch(action_sceneAssetsLoadingProgressChanged(0));
        loadAssets(assets, assetVersion, (e) => {
            setPeogress(Math.floor(e * 100));
        });
    }, []);

    useFrame(400, () => {
        setTextIndex((textIndex + 1) % 3);
    }, true)

    return <Scene {...props}>
        <ProgressBarTweenLayer progress={progress} onComplete={onProgressComplete}>
            {
                (progress: number) => {
                    return children(progress, text);
                }
            }
        </ProgressBarTweenLayer>
    </Scene>
}