import { ISkeletonData } from 'pixi-spine';
import { Assets, Spritesheet, Texture } from 'pixi.js';
import { SlotContext } from '../model/context';

export interface KRrsourceOptions {
    readonly type?: 'file' | 'rawAtlas',
    readonly id: string,
    readonly src?: string,
    readonly options?: any
}

export function getResourceUrl(e: string, version: number): string {
    return `./assets/${e}${version ? `?v=${version}` : ""}`
}

function resolveUrl(src: string, version: number) {
    const match = src.match(/\[(.+)\]/);
    if (match) {
        let formats = match[1].split(',');
        src = src.replace(match[0], SlotContext.Obj.filterImageFormat(formats));
    }
    return getResourceUrl(src, version);
}

export function loadAssets(configs: readonly KRrsourceOptions[], version: number, onProgress: (e: number) => void): Promise<unknown> {
    let ids = [];
    function addResource(id: string, src: string, options = {}) {
        Assets.add({
            src: resolveUrl(src, version),
            alias: id,
            ...options
        });
    }

    for (let item of configs) {
        ids.push(item.id);
        switch (item.type) {
            default: addResource(item.id, item.src, item.options);
        }
    }

    return Assets.load(ids, onProgress);
}

export function getImg(id: string): Texture {
    let t = Assets.get(id);
    return t;
}

export function getTexture(id: string, name: string): Texture {
    let t: Spritesheet = Assets.get(id);
    let texture = t.textures[name];
    if (!texture) {
        for (let sheet of t.linkedSheets) {
            texture = sheet.textures[name];
            if (texture) break;
        }
    }
    return texture;
}

//https://github.com/pixijs/pixijs/pull/8859
export function getTexturesBySpritesheet(id: string, name: string): Texture[] {
    let t: Spritesheet = Assets.get(id);
    let textures = t.animations[name];
    if (!textures) {
        for (let sheet of t.linkedSheets) {
            textures = sheet.animations[name];
            if (textures) break;
        }
    }
    return textures;
}

export function getTextures(id: string, name: string): Texture[] {
    let t: Spritesheet = Assets.get(id);
    const animation = t.data.animations[name];
    const textures: Texture[] = [];
    for (let name of animation) {
        textures.push(getTexture(id, name));
    }
    return textures;
}

export function getJson(id: string) {
    let t = Assets.get(id);
    return t;
}

export function getLang(id: string, key: string): string {
    let t = Assets.get(id) || {};
    return t[key] || key;
}

export function getSpineData(id: string): ISkeletonData {
    let t = Assets.get(id);
    return t.spineData;
}