import { IOrientationType } from "../../util/constant";
import { KPlatformLayoutOptions } from "./baseContext";
import { EventContext } from "./eventContext_2";

export class LayoutContext extends EventContext {
    private _viewport: [number, number, number, number];
    private _orientation: IOrientationType;

    get viewport(): [number, number, number, number] {
        return this._viewport;
    }

    get orientation(): IOrientationType {
        return this._orientation;
    }

    updateViewport(left: number, top: number, width: number, height: number) {
        this._viewport = [left, top, width, height];
    }

    updateOrientation() {
        this._orientation = this.getOrientationType();
    }

    private getOrientationType(): IOrientationType {
        const map = this.configOptions.layoutMap;
        if (map.p && map.l) {
            return (document.documentElement.clientHeight / document.documentElement.clientWidth) > (1 / 0.767) ? 'p' : 'l';
        } else if (map.p) {
            return 'p';
        } else {
            return 'l';
        }
    }

    get layoutOptions(): KPlatformLayoutOptions {
        return this.configOptions.layoutMap[this._orientation];
    }
}