import Decimal from "decimal.js"
import { ACTION_SIGN_IN, ACTION_BALANCE_CHANGED, ACTION_GET_PLAYER_INFO, KReducerActionOptions } from "./actionTypes"
import { PlayerModel } from "../util/parser/player"

export interface KUserReducerOptions {
    user: {
        loggingIn: boolean,
        memberName: string,
        currency: string,
        isTestAccount: boolean,
        balance: Decimal
    }
}

export const user = (state: any = {}, action: KReducerActionOptions) => {
    switch (action.type) {
        case ACTION_SIGN_IN:
            return {
                //update name, is test accounr, currency
            }
        case ACTION_GET_PLAYER_INFO:
            const model: PlayerModel = action.value;
            return {   
                ...state,
                loggingIn: true,
                memberName: model.name,
                isTestAccount: model.test,
                balance: model.balance,
                currency: model.currency
            }
        case ACTION_BALANCE_CHANGED: 
            return {   
                ...state,
                balance: action.value.balance,
            }
        default:
            return state
    }
}

export const userInitialState: KUserReducerOptions = {
   user: {
    loggingIn: false,
    memberName: 'w88',
    currency: 'RMB',
    isTestAccount: true,
    balance: new Decimal(0)
   }
}

export function action_signIn(): KReducerActionOptions {
    return {
        type: ACTION_SIGN_IN
    }
}

export function action_getPlayerInfo(model: PlayerModel): KReducerActionOptions {
    return {
        type: ACTION_GET_PLAYER_INFO,
        value: model
    }
}

export function action_balanceChanged(balance: Decimal.Value): KReducerActionOptions{
    return {
        type: ACTION_BALANCE_CHANGED,
        value: {
            balance: new Decimal(balance)
        }
    }
}