import { Stage, useTick } from "@pixi/react";
import { update as tweenUpdate } from '@tweenjs/tween.js'
import { Children, ComponentProps, useReducer, useRef } from "react";
import { GameContext } from "../AppContext";
import { getCombineReducers, getInitState } from '../model';

import { ApiFC } from '../util/api';
import { LayoutServiceFC } from '../services/layoutService';
import { ScenePreloadLoading } from "../components2/loading";
import { SlotContext } from "../model/context";
import { SoundServiceFC } from "../services/soundServiceFC";
import { LaunchService } from "../services/launchService";
import { ErrorService } from "../services/errorService";
import {utils} from 'pixi.js'

interface KGameOptions extends ComponentProps<typeof Stage>{
    children: any,
    maxResolution: number
}

const ContextBridge = ({ children, Context, render }) => {
    return (
        <Context.Consumer>
            {(value) => {
                return render(<Context.Provider value={value}>
                    <ApiFC></ApiFC>
                    <Gameconfig></Gameconfig> 
                    <LayoutServiceFC></LayoutServiceFC>
                    <SoundServiceFC></SoundServiceFC>
                    <LaunchService></LaunchService>
                    <ErrorService></ErrorService>
                    {children}
                </Context.Provider>)
            }}
        </Context.Consumer>
    )
}

const reducers = getCombineReducers();
const initState: any = getInitState();

export const Game = (({children, maxResolution = 1, ...props}: KGameOptions) => {
    const [state, dispatch] = useReducer(reducers, initState);
    let [gamechild, ...child] = Children.map(children, e => e);

    let _resolution = utils.isMobile.any ? window.devicePixelRatio : 1; 
    _resolution = Math.min(_resolution >= 2 ? 2 : _resolution, maxResolution); 
    return (
        <GameContext.Provider value={{state: state, dispatch: dispatch}}>
            <ContextBridge Context={GameContext} 
                            render={children => <Stage options={{ backgroundColor:  0x111111, backgroundAlpha: 0, resolution: _resolution, antialias: true, autoDensity: true }} {...props}>
                                                    {children}
                                                </Stage>
                                    }>{gamechild}</ContextBridge>
            <ScenePreloadLoading></ScenePreloadLoading>
            <GameContext.Consumer>
                {
                    ({state}) => {
                        
                        return state.sceneManager.scenes[0] == 'preload' ? <></> : child;
                    }
                }
            </GameContext.Consumer>
        </GameContext.Provider>
    )
});


export function Gameconfig() {
    const slotContext = SlotContext.Obj;
    useTick((delta, ticker) => {
        tweenUpdate(slotContext.tweenTime);
        slotContext.tweenTime += ticker.elapsedMS;
    }, true);
    return <></>;
}
