import { isBlank, isPresent, isEmptyObject } from "../util/lang";
import { Howler, Howl } from 'howler';
import { getJson } from "../util/assetsLoad";

type Playback = {
    channelId: string;
    name: string;
    soundId: number;
    volume: number;
    softBgm?: boolean,
    
    onComplete?: (soundName: string) => void;
}

class ChannelManager {
    channels: { [channelId: string]: { [soundName: string]: Playback } } = {};

    exist(channelId: string, soundName: string) {
        let playbackMap = this.channels[channelId];
        return isPresent(channelId) && isPresent(playbackMap[soundName]);
    }

    add(channelId: string, soundName: string, soundId: number, volume = 1, softBgm = false, onComplete?: (soundName) => void) {
        let playbackMap = this.channels[channelId];
        if (isBlank(playbackMap)) {
            playbackMap = {};
            this.channels[channelId] = playbackMap;
        }
        if (isPresent(playbackMap[soundName])) {
            console.warn(`sound "${soundName}" is existing in channel "${channelId}"`);
        }
        let playback = {
            channelId: channelId,
            name: soundName,
            soundId: soundId,
            volume: volume,
            softBgm: softBgm,
            onComplete: onComplete
        };
        playbackMap[soundName] = playback;
        return playback;
    }

    getPlaybackBy(predicate: (playback: Playback) => boolean): Playback {
        for (let channelKey in this.channels) {
            let playbackMap = this.channels[channelKey];
            for (let playbackKey in playbackMap) {
                if (predicate(playbackMap[playbackKey])) {
                    return playbackMap[playbackKey];
                }
            }
        }
    }

    getPlaybackBySoundId(e: number): Playback {
        return this.getPlaybackBy(playback => playback.soundId == e);
    }

    getPlaybackBySoundName(e: string): Playback {
        return this.getPlaybackBy(playback => playback.name == e);
    }

    removeBy(predicate: (playback: Playback) => boolean) {
        for (let channelKey in this.channels) {
            let playbackMap = this.channels[channelKey];
            for (let playbackKey in playbackMap) {
                let playback = playbackMap[playbackKey];
                if (predicate(playback)) {
                    delete playbackMap[playbackKey];
                    if (isEmptyObject(playbackMap)) {
                        delete this.channels[channelKey];
                    }
                }
            }
        }
    }

    removeBySoundId(e: number) {
        return this.removeBy(playback => playback.soundId == e);
    }

    removeBySoundName(e: string) {
        return this.removeBy(playback => playback.name == e);
    }

    getSoundIds(channelId): number[] {
        const ids = [];
        const channel = this.channels[channelId];

        for (let name in channel) {
            ids.push(channel[name].soundId);
        }
        return ids;
    }
}

export class SoundService {
    private howlers: { [channelId: string]: Howl } = {};
    private _muted: boolean = false;
    private _volume: number = 1;
    private bgm: { channelId: string, name: string, soft: number, soundId: number };
    private channelManager: ChannelManager;
    private _pause: boolean;

    private static _Obj: SoundService;
    static get Obj(): SoundService {
        if (!this._Obj) {
            this._Obj = new SoundService();
        }
        return this._Obj;
    }
    constructor() {
        this.channelManager = new ChannelManager();
    }

    getSoundIdBySoundName(name: string) {
        let playback = this.channelManager.getPlaybackBySoundName(name);
        if (playback) return playback.soundId;
    }

    getChannelIdBySoundName(name: string) {
        let playback = this.channelManager.getPlaybackBySoundName(name);
        if (playback) return playback.channelId;
    }

    play(channelId: string, soundName: string, volume = 1, softBgm: boolean = false, loop: boolean, onEnd?: (soundName: string) => void) {
        let playback = this.channelManager.getPlaybackBySoundName(soundName),
        realVolumn = this.getVolumn(volume);

        let howl = this.getSoundPlayer(channelId);
        
        if (playback && playback.channelId == channelId) {
            //。。。 还未播放完的音乐
            howl.stop(playback.soundId);
            howl.play(playback.soundId);
            howl.volume(realVolumn, playback.soundId);
        } else {
            let soundId = howl.play(soundName);
            howl.volume(realVolumn, soundId);
            playback = this.channelManager.add(channelId, soundName, soundId, volume, softBgm, (soundName) => {
                onEnd && onEnd(soundName);
            })
        }
        if (softBgm) this.softBgm(true);
    }

    plays(channelId: string, soundNames: string[], volume = 1, softBgm: boolean = false, onEnd?: (soundName: string) => void) {
        const [soundName, ...sounds] = soundNames;
        if (soundName) {
            this.play(channelId, soundName, volume, softBgm, false, () => {
                this.plays(channelId, sounds, volume, softBgm, onEnd);
            });
        } else {
            onEnd && onEnd(soundName);
        }
    }

    playBgm(channelId: string, soundName: string, volume = 1, soft: number = 0.3) {
        let playback = this.channelManager.getPlaybackBySoundName(soundName);
        let howl = this.getSoundPlayer(channelId);
        let realVolumn = this.getVolumn(volume);

        if (playback && playback.channelId == channelId) {
            if (!howl.playing(playback.soundId)) {
                howl.play(playback.soundId);
                howl.volume(realVolumn, playback.soundId);
            } 
        } else {
            this.bgm && this.stop(this.bgm.channelId, this.bgm.name);
            let soundId = howl.play(soundName);
            howl.volume(realVolumn, soundId);
            this.channelManager.add(channelId, soundName, soundId, volume);
            this.bgm = {
                soundId: soundId,
                channelId: channelId,
                name: soundName,
                soft: soft
            };
        }
    }

    private isReady(channelId: string): boolean {
        return isPresent(this.howlers[channelId]);
    }

    private getSoundPlayer(channelId: string): Howl {
        if (!this.isReady(channelId)) {
            let options = getJson(channelId);
            if (isPresent(options)) {
                options.onend = (soundId: number) => {
                    if (this.bgm && soundId == this.bgm.soundId) {
                        
                    } else {
                        let playback = this.channelManager.getPlaybackBySoundId(soundId);
                        if (playback && playback.name.indexOf('bgm') == -1) { // playback &&  ???
                            if (playback) {
                                playback.softBgm && this.softBgm(false);
                                playback.onComplete && playback.onComplete(playback.name);
                            }
                            this.channelManager.removeBySoundId(soundId);
                        }
                    }
                }
                let howl = new Howl(options);
                this.howlers[channelId] = howl;
            }
        }
        return this.howlers[channelId];
    }

    pauses(v: boolean) {
        if (this._pause != v) {
            for (let channelId in this.howlers) {
                if (v) {
                    this.howlers[channelId].pause();
                } else {
                    const ids = this.channelManager.getSoundIds(channelId);
                    const howl = this.howlers[channelId];
                    for (let id of ids) {
                       !howl.playing(id) && howl.play(id);
                    }
                }
            }
            this._pause = v;
        }
    }

    pause(channelId: string, soundName: string) {
        let soundId = this.getSoundIdBySoundName(soundName);
        soundId && this.getSoundPlayer(channelId).pause(soundId);
    }

    stop(channelId: string, soundName: string) {
        let soundId = this.getSoundIdBySoundName(soundName);
        if (soundId) {
            this.channelManager.removeBySoundId(soundId);
            this.getSoundPlayer(channelId).stop(soundId);
        }
    }

    stops(channelId: string, soundNames: string[]) {
        soundNames.forEach(e => this.stop(channelId, e));
    }

    softBgm(e: boolean) {
        if (this.bgm) {
            let playback = this.channelManager.getPlaybackBySoundName(this.bgm.name)
            if (playback) {
                let volume = this.getVolumn(e ? this.bgm.soft : 1, playback.volume);
                this.getSoundPlayer(this.bgm.channelId).volume(volume, playback.soundId);
            }
        }
    }

    getVolumn(...args: number[]) {
        const t = args.reduce((previousValue, currentValue) => {
            return previousValue * currentValue
        }, this._volume);
        return t;
    }

    setVolumn(channelId: string, soundName: string, volumn: number) {
        let soundId = this.getSoundIdBySoundName(soundName);
        if (soundId) {
            const playBack = this.channelManager.getPlaybackBySoundId(soundId);
            playBack.volume = volumn;
            this.getSoundPlayer(channelId).volume(volumn);
        }
    }

    get volume(): number {
        return this._volume;
    }

    set volume(e: number) {
        if (this._volume != e) {
            this._volume = e;
            for (let channelId in this.channelManager.channels) {
                let channel = this.channelManager.channels[channelId];
                let howl = this.getSoundPlayer(channelId);
                for (let id in channel) {
                    let playback = channel[id];
                    howl.volume(playback.volume * e, playback.soundId);
                }
            }
        }
    }

    get muted(): boolean {
        return this._muted;
    }

    set muted(e: boolean) {
        this._muted = e;
        Howler.mute(e);
    }
}