import { useContext, useEffect } from "react";
import { KReducerActionOptions } from "../model/actionTypes";
import { SlotContext } from "../model/context";
import { action_error_detail } from "../model/setting";
import { getApiService, getHttpParser } from "../other/register";
import { GameContext } from "../AppContext";
import { isString } from "./lang";

export interface KHttpServiceOptions {
    authAndCanReTry: () => boolean;
    auth: () => Promise<any>;
    player: () => Promise<any>
    meta: () => Promise<any>
    load: () => Promise<any>
    spin: (bet: number) => Promise<any>
    spin2: (bet: number, level: number) => Promise<any>
    start: (bet: number) => Promise<any>
    continue: () => Promise<any>
    lineBets: () => Promise<any>
    history: (start: number, end: number, page: number, size: number, order: 0 | 1) => Promise<any>
    setDispatch: (e: (e: KReducerActionOptions) => any) => void
}

const cookieAuthName = 'authorization';
const uatDonmain = 'https://dev-crescendo-real.gpistudios.com';
export const funplayDomain = 'http://localhost:8081'//'https://dev-crescendo-slot-api.gpistudios.com'; //'http://localhost:8081';

export class ApiService implements KHttpServiceOptions {
    private context: SlotContext = SlotContext.Obj;
    private authorization: string = sessionStorage.getItem(cookieAuthName);     
    private canReAuth = !!this.authorization;
    private _dispatch: (e: KReducerActionOptions) => any;

    get domain(): string {
        return this.context.urlOptions.funPLay ? funplayDomain : uatDonmain;
    }

    setDispatch(e) {
        this._dispatch = e;
    }

    authAndCanReTry() {
        this.authorization = '';
        sessionStorage.setItem(cookieAuthName, '');

        const reAuth = this.canReAuth;
        this.canReAuth = false;
        return reAuth;
    }

    protected request(url, method: 'GET' | "POST" = "GET", type: 'json' | 'text' = 'json', Model: any = null): Promise<any> {
        const headers = this.authorization ? {
            "Authorization": `${this.authorization}`
        } : {}

        if (navigator.onLine == false) {
            this._dispatch(action_error_detail('000'));
            return Promise.reject();
        } else {
            return new Promise((resolve, reject) => {
                const controller = new AbortController();

                const timer = setTimeout(() => {
                    clearTimeout(timer);
                    controller.abort();
                    this._dispatch(action_error_detail('001'));
                    reject();
                    //
                }, 10000);

                fetch(url, {
                    method: method,
                    headers: headers,
                    signal: controller.signal
                }).then((response) => {
                    clearTimeout(timer);
                    if (response.status == 200) {
                        const json = response[type]();
                        json.then((e) => {
                            if (Model) {
                                const model = new Model(e);
                                if (model.code) {
                                    this._dispatch(action_error_detail(model.code));
                                }
                                resolve(model);
                            } else {
                                resolve(e)
                            }
                        }).catch(() => {
                            this._dispatch(action_error_detail('000'));
                            reject()
                        })
                    } else {
                        this._dispatch(action_error_detail(response.status));
                        resolve({code: response.status});
                    }
                }).catch((error: Error) => {
                    clearTimeout(timer);
                    this._dispatch(action_error_detail('000'));
                    reject();
                });
            });
        }
    }

    auth() {
        const { operator, token: _token } = this.context.urlOptions;
        const token = _token || Math.random().toFixed(8);
        const url = `${this.domain}/auth/${operator}/${token}`;

        if (!_token && this.authorization) {
            return Promise.resolve(this.authorization);
        } else {
            return this.request(url, 'GET', 'text').then((authorization: string) => {
                if (isString(authorization)) {
                    authorization = `Bearer ${authorization}`;
                    this.authorization = authorization;
                    sessionStorage.setItem(cookieAuthName, authorization);
                }
            });
        }
    }

    player() {
        const Model = getHttpParser('player');
        const url = `${this.domain}/s/player`;
        return this.request(url, 'GET', 'json', Model);
    }

    meta() {
        const Model = getHttpParser('meta');
        const gameId = this.context.configOptions.id;
        const url = `${this.domain}/s/meta/${gameId}`;
        return this.request(url, 'GET', 'json', Model);
    }

    spin(payout: number) {
        const Model = getHttpParser('spin');
        const gameId = this.context.configOptions.id;
        // const url = `${this.domain}/s/spin/${gameId}/${payout}`;
        const url = `${this.domain}/s/spin/${gameId}?lineBet=${payout}`;
        return this.request(url, 'GET', 'json', Model);
    }

    spin2(payout: number, level: number) {
        const Model = getHttpParser('spin');
        const gameId = this.context.configOptions.id;
        const url = `${this.domain}/s/spin/${gameId}?lineBet=${payout}&level=${level}`;
        return this.request(url, 'GET', 'json', Model);
    }

    load() {
        const Model = getHttpParser('load');
        const gameId = this.context.configOptions.id;
        const url = `${this.domain}/s/load/${gameId}`;
        return this.request(url, 'GET', 'json', Model);
    }

    start(payout: number) {
        const Model = getHttpParser('start');
        const gameId = this.context.configOptions.id;
        const url = `${this.domain}/s/start/${gameId}/${payout}`;
        return this.request(url, 'GET', 'json', Model);
    }

    continue() {
        const Model = getHttpParser('continue');
        const gameId = this.context.configOptions.id;
        const url = `${this.domain}/s/cont/${gameId}`;
        return this.request(url, 'GET', 'json', Model);
    }

    //0 asc 1 desc
    history(start: number, end: number, page: number, size: number, order: 0 | 1) {
        const Model = getHttpParser('history');
        const gameId = this.context.configOptions.id;
        const url = `${this.domain}/s/list/${gameId}/${start}/${end}/${page}/${size}/${order}`;
        return this.request(url, 'GET', 'json', Model);
    }

    lineBets() {
        const Model = getHttpParser('lineBets');
        const gameId = this.context.configOptions.id;
        const url = `${this.domain}/s/lineBets/${gameId}`;
        return this.request(url, 'GET', 'json', Model);
    }
}

export function ApiFC() {
    const { state, dispatch } = useContext(GameContext);

    useEffect(() => {
        const api = getApiService();
        api.setDispatch(dispatch);
    }, []);

    return <></>
}