import Decimal from 'decimal.js';
import { BaseSpinModel } from './spin';
import { SlotContext } from '../../../model/context';
import { mapWith } from '../../array';

// {"ss":[[7,8,3],[2,7,4],[0,1,5],[1,7,2],[4,9,7]],"sm":0,"lm":12,"lw":{"l2r":[[3,7,2,3]],"r2l":[[8,9,1,2],[7,9,1,2],[4,7,2,3],[0,9,1,2]]}}
// line，　symbol，count， multiplier

/*
*   queenbee
*/
export type KWinlineParamsDirectorType = 'l2r' | 'r2l';
export type KWinlineParamsPositionSymbolArr = [number, number, number?];

export interface KSpinModelOptions {
    readonly code?: number;
    readonly message?: string;
    readonly credit: Decimal;
    readonly cost: Decimal;
    readonly payout: Decimal;
    readonly balance: Decimal;

    readonly columnSymbolsMap: { [key: string]: number[] };
    readonly linewins: KLineWinOptions[];
    readonly lineMultiplier?: Decimal;
    readonly wildReels?: readonly number[];
    readonly columnWinSymbolsRowMap?: { [column: number]: number[] };
}

export interface KLineWinOptions {
    readonly id?: number,
    readonly type: number,      //win symbol type
    readonly multiplier: Decimal;
    readonly columnRowSymbolArr: KWinlineParamsPositionSymbolArr[];   //column, row, symbol
    readonly columnMapRowSymbolMap: { [column: string | number]: { [row: string | number]: number } };
    readonly path?: [number, number][];              //win line
    readonly director?: KWinlineParamsDirectorType;              //win line director
    readonly scatterWin?: boolean;
    readonly existWild: boolean;
}

export class SpinModel extends BaseSpinModel {
    readonly columnSymbolsMap: { [key: string]: number[] };
    readonly linewins: KLineWinOptions[];   //lw line win
    readonly scatterLinewins: KLineWinOptions[];

    readonly scaterMultiplier: number;    //sm => scater multiplier
    readonly lineMultiplier: Decimal;       //lm => line multiplier

    constructor(data) {
        super(data);
        if (this.code) return;
        const table = SlotContext.Obj.tableOptions;

        const result = data.result;

        this.scaterMultiplier = result.sm;
        this.lineMultiplier = new Decimal(result.lm);

        const columnSymbolsMap = {};
        result.ss.forEach((reel, i) => columnSymbolsMap[i] = reel);
        this.columnSymbolsMap = columnSymbolsMap;

        let lineWins: KLineWinOptions[] = [];
        let scatterLinewins: KLineWinOptions[] = [];
        if (result.lw) {
            for (let key in result.lw) {
                for (let item of result.lw[key]) {
                    let id = item[0], count = item[2];
                    let line: KLineWinOptions = {
                        id: id,
                        type: item[1],
                        multiplier: new Decimal(item[3]),
                        columnRowSymbolArr: [...table.paylineMap[id]].splice(key == 'l2r' ? 0 : table.column - count, count).map(e => [...e, this.columnSymbolsMap[e[0]][e[1]]]),
                        columnMapRowSymbolMap: {},
                        path: table.paylinePathMap[id],
                        director: key as any,
                        scatterWin: false,
                        existWild: item[4]
                    }
                    lineWins.push(line);
                }
            }
        }
        if (result.sw) {
            for (let key in result.sw) {
                for (let item of result.sw[key]) {
                    const multiplier = new Decimal(item[1]), count = item[0];
                    let line: KLineWinOptions = {
                        id: null,
                        type: 10,
                        multiplier: multiplier,
                        columnRowSymbolArr: mapWith(count, (i) => {
                            let inColumn, inRow, s = 11;
                            if (key == 'l2r') {
                                inColumn = i;
                                inRow = result.ss[inColumn].indexOf(11);
                                if (inRow == -1) {
                                    s = 10;
                                    inRow = result.ss[inColumn].indexOf(10);
                                }
                            } else {
                                inColumn = 4 - i;
                                inRow = result.ss[inColumn].indexOf(11);
                                if (inRow == -1) {
                                    s = 10;
                                    inRow = result.ss[inColumn].indexOf(10);
                                }
                            }
                            return [inColumn, inRow, s]
                        }),
                        columnMapRowSymbolMap: {},
                        path: null,
                        director: key as any,
                        scatterWin: true,
                        existWild: item[2]
                    }
                    lineWins.push(line);
                    scatterLinewins.push(line);
                }
            }
        }

        this.linewins = lineWins.sort((a, b) => {
            if (a.director == b.director) {
                return a.id - b.id;
            } else if (a.director == 'l2r') {
                return -1;
            } else {
                return 1;
            }
        })

        this.scatterLinewins = scatterLinewins;
    }

    get isExist5OfAKind(): boolean {
        for (let item of this.linewins) {
            if (item.columnRowSymbolArr.length == 5) return true;
        }
        return false;
    }

    get totalMultiplier(): Decimal {
        return this.lineMultiplier.add(this.scaterMultiplier);
    }

    get winType(): 'high' | 'big' | 'normal' | 'small' | 'medium' {
        const t = this.payout.div(this.cost);
        if (t.greaterThanOrEqualTo(38)) return 'big';
        else if (t.greaterThanOrEqualTo(26)) return 'high';
        else if (t.greaterThanOrEqualTo(17)) return 'medium';
        else if (t.greaterThanOrEqualTo(6)) return 'normal';
        else return 'small';
    }

    get isBigWin(): boolean {
        return this.winType == 'big';
    }
}
