import { IOrientationType } from '../util/constant';
import { ACTION_AUTO_PANEL_OPEN, ACTION_BET_PANEL_OPEN, ACTION_ERROR_DETAIL, ACTION_FOTCE_UPDATE, ACTION_HISTORY_DETAIL_OPEN, ACTION_HISTORY_LIST_OPEN, ACTION_MENU_OPEN, ACTION_ORIENTATION_CHANGED, ACTION_PAYTABLE_OPEN, ACTION_SOUND_OPEN, ACTION_VOLUME_CHANGED, KReducerActionOptions } from './actionTypes';
import * as QueryString from '../util/querystring';

export interface KSettingReducerOptions {
    setting: {
        soundOn: boolean,
        volume: number,
        menuOn: boolean,
        payTableOn: boolean,
        betPanelOn: boolean,
        autoSpinPanelOn: boolean,
        orientation: IOrientationType,
        forceUpdate: boolean,
        historyListOn: boolean,
        historyDetail: {
            on: boolean,
            data: any
        },
        error: {
            code: number | string,
            show: boolean,
            allowClose: boolean
        }
    }
}

export const setting = (state: any = {}, action: KReducerActionOptions) => {
    switch(action.type) {
        case ACTION_SOUND_OPEN: 
            return {
                ...state,
                soundOn: action.value
            }
        case ACTION_VOLUME_CHANGED:
            return {
                ...state
            }
        case ACTION_MENU_OPEN:
            return {
                ...state,
                menuOn: action.value
            }
        case ACTION_PAYTABLE_OPEN: 
            return {
                ...state,
                payTableOn: action.value
            }
        case ACTION_BET_PANEL_OPEN:
            return {
                ...state,
                betPanelOn: action.value
            }
        case ACTION_AUTO_PANEL_OPEN:
            return {
                ...state,
                autoSpinPanelOn: action.value
            }
        case ACTION_ORIENTATION_CHANGED:
            return {
                ...state,
                orientation: action.value
            }
        case ACTION_FOTCE_UPDATE:
            return {
                ...state,
                forceUpdate: !state.forceUpdate
            }
        case ACTION_HISTORY_LIST_OPEN:
            return {
                ...state,
                historyListOn: action.value
            }
        case ACTION_HISTORY_DETAIL_OPEN:
            return {
                ...state,
                historyDetail: {
                    ...action.value
                }
            }
        case ACTION_ERROR_DETAIL:
            return {
                ...state,
                error: {
                    ...action.value
                }
            }
        default:
            return state;
    }

}

export const settingInitialState: KSettingReducerOptions = {
    setting: {
        volume: 0.5,
        menuOn: false,
        payTableOn: false,
        betPanelOn: false,
        autoSpinPanelOn: false,
        orientation: 'l',
        forceUpdate: false,
        historyListOn: false,
        historyDetail: {
            on: false,
            data: null
        },
        error: {
            code: 0,
            show: false,
            allowClose: true,
        }
    }
}

export function action_soundOpen(e: boolean): KReducerActionOptions {
    return {
        type: ACTION_SOUND_OPEN,
        value: e
    }
}

export function action_volumnChanged(volumn: number): KReducerActionOptions {
    return {
        type: ACTION_VOLUME_CHANGED,
        value: volumn
    }
}
    
export function action_menuOpen(e: boolean): KReducerActionOptions {
    return {
        type: ACTION_MENU_OPEN,
        value: e
    }
}

export function action_paytableOpen(e: boolean): KReducerActionOptions {
    return {
        type: ACTION_PAYTABLE_OPEN,
        value: e
    }
}

export function action_betPanelOpen(e: boolean): KReducerActionOptions {
    return {
        type: ACTION_BET_PANEL_OPEN,
        value: e
    }
}

export function action_autoPanelOpen(e: boolean): KReducerActionOptions {
    return {
        type: ACTION_AUTO_PANEL_OPEN,
        value: e
    }
}

export function action_setOrientation(e: IOrientationType): KReducerActionOptions {
    return {
        type: ACTION_ORIENTATION_CHANGED,
        value: e
    }
}

export function action_forceUpdate(): KReducerActionOptions {
    return {
        type: ACTION_FOTCE_UPDATE
    }
}

export function action_history_list_open(e: boolean): KReducerActionOptions {
    return {
        type: ACTION_HISTORY_LIST_OPEN,
        value: e
    }
}

export function action_history_detail_open(e: boolean, data?: any): KReducerActionOptions {
    return {
        type: ACTION_HISTORY_DETAIL_OPEN,
        value: {
            on: e,
            data: data
        }
    }
}

export function action_error_detail(code: number | string, show: boolean = false, allowClose: boolean = true): KReducerActionOptions {
    return {
        type: ACTION_ERROR_DETAIL,
        value: {
            code: code,
            show: show,
            allowClose: allowClose
        }
    }
}