import { Languages } from './model/context/baseContext';
import { mapWith } from './util/array';
import { KRrsourceOptions } from './util/assetsLoad';

function merge(...args: KRrsourceOptions[][]): KRrsourceOptions[] {
    let map = {};
    for (let configs of args) {
        for (let config of configs) {
            map[config.id] = config;
        }
    }
    return Object.values(map);
}

export const R_Game_Bg_L = 'game_Bg_L'
export const R_Game_Bg_P = 'game_Bg_P'
export const R_Message = 'message';
export const R_Lang_Components = 'lang_components';
function getPreloadAssetsConfig(lang: Languages): KRrsourceOptions[]  {
    return [
        {   id: R_Message,
            src: `text/${lang}.json`
        },
        {
            id: R_Lang_Components,
            src: `compress/lang/${lang}@1x.[avif,webp,png].json`
        }
    ]
}

export function mergePreloadAssetsConfig(lang: Languages, ...args: KRrsourceOptions[][]): KRrsourceOptions[] {
    return merge(getPreloadAssetsConfig(lang), ...args);
}

export const R_Symbol = 'symbol';
export const R_Sounds = 'sounds';
export const R_Components = 'components';
export const R_Uncomponents = 'uncomponents';
export const R_PreloadComponent = 'preload_component';
export function getLoadingAssetsConfig(lang: Languages): KRrsourceOptions[]  {
    return [
        {
            id:R_Sounds,
            src: 'sound/sounds.json'
        }, {
            id: R_Components,
            src: 'compress/components-_default@1x.[avif,webp,png].json'
        }
    ];
}

export function mergeLoadingAssetsConfig(lang: Languages, ...args: KRrsourceOptions[][]): KRrsourceOptions[] {
    return merge(getLoadingAssetsConfig(lang), ...args);
}