import * as QueryString from '../../util/querystring';
import { isMobile } from 'pixi.js'
import { IOrientationType, IPlatform } from '../../util/constant';
import { getReducerInitial } from '../../other/register';
import { isNumber } from '../../util/lang';
import { randomInArr } from '../../util/array';

export interface KGameURLParamsOption  {
    readonly operator: string,
    readonly funPLay: boolean,
    readonly showLog: boolean,
    readonly token: string | undefined,
    readonly lang: Languages,
    readonly platform: KPlatformType,
    readonly loginUrl?: string
    readonly lobbyUrl?: string
    readonly fundsUrl?: string;
    readonly livechatUrl?: string;
    // readonly mute: boolean,
}

export interface KGameTableOptions {
    readonly row: number | {[key: number]: number},
    readonly column: number,
    readonly paylineMapV2: {readonly [key: string]: number[]};
    readonly paylineMap: {readonly [key: string]: [number,number][]};
    readonly paylinePathMap: {readonly [key: string]: [number, number][]}
    readonly bonusSymbols?: number[]
    readonly symbolSize: [number, number];
    readonly symbolFrame: readonly [number, number]
}

export type KPlatformLayoutOptions = {
    readonly displayArea: [number, number, number, number],
    readonly safeArea: [number, number, number, number]   
}

export interface KGameConfigOptions {
    readonly id: number;
    readonly assetVersion?: number,
    readonly platform?: IPlatform,
    readonly layoutMap: {
        readonly l?: KPlatformLayoutOptions,
        readonly p?: KPlatformLayoutOptions
    }
}

export type KPlatformType = 'web' | 'mobile' | 'mini' | 'desktop';

export enum Languages {
    English = "en",
    Chinese = "cs",
    Vietnamese = "vn",
    Thai = "th",
    Khmer = "kh", // Cambodia
    Indonesian = "id",
    Korean = "kr",
    Japanese = "jp"
}

export class BaseContext {
    readonly urlOptions: KGameURLParamsOption;
    readonly tableOptions: KGameTableOptions;
    readonly configOptions: KGameConfigOptions;
    
    constructor() {
        this.urlOptions = this.getUrlConfig();
        this.tableOptions = getReducerInitial<KGameTableOptions>('table') || {};
        this.configOptions = getReducerInitial<KGameConfigOptions>('gameconfig') || {};
    }

    protected configureLanguage(): Languages {
        let lang: Languages = QueryString.getParameter('lang', Languages.English) as Languages;
        switch (true) {
          case (lang.indexOf("us") >= 0):
          case (lang.indexOf("en") >= 0): lang = Languages.English; break;
          case (lang.indexOf("cn") >= 0):
          case (lang.indexOf("zh") >= 0):
          case (lang.indexOf("zn") >= 0):
          case (lang.indexOf("cs") >= 0): lang = Languages.Chinese; break;
          case (lang.indexOf("th") >= 0): lang = Languages.Thai; break;
          case (lang.indexOf("vi") >= 0):
          case (lang.indexOf("vn") >= 0): lang = Languages.Vietnamese; break;
          case (lang.indexOf("ko") >= 0):
          case (lang.indexOf("kr") >= 0): lang = Languages.Korean; break;
          case (lang.indexOf("km") >= 0):
          case (lang.indexOf("kh") >= 0): lang = Languages.Khmer; break;
          case (lang.indexOf("id") >= 0): lang = Languages.Indonesian; break;
          case (lang.indexOf("ja") >= 0):
          case (lang.indexOf("jp") >= 0): lang = Languages.Japanese; break;
          default: lang = Languages.English;
        }
        return lang;
    }
    
    protected getUrlConfig(): KGameURLParamsOption {
        const obj = {
            operator: QueryString.getParameter('op', ''),
            funPLay: QueryString.getParameter('fun', '0') == '1',
            showLog: QueryString.getParameter('log', '0') == '1',
            loginUrl: QueryString.getParameter('loginUrl', ''),
            lobbyUrl: QueryString.getParameter('lobbyUrl', ''),
            fundsUrl: QueryString.getParameter('fundsUrl', ''),
            livechatUrl: QueryString.getParameter('livechatUrl', ''),
            // mute: QueryString.getParameter('mute', false) == '1',
            token: QueryString.getParameter('token') || QueryString.getParameter('s'),
            lang: this.configureLanguage(),
            platform: (QueryString.getParameter('p') || (isMobile.any ? 'mobile' : 'web')) as KPlatformType
        };
        if (!obj.token && !obj.loginUrl) obj.funPLay = true;
        return obj;
    }

    getSymbolColumnRowKey(inColumn: number, inRow: number): string {
        return `${inColumn}_${inRow}`;
    }

    loopForReels(cb: (inColumn: number, inRow: number) => void) {
        const column = this.tableOptions.column, row = this.tableOptions.row;
        for (let inColumn = 0; inColumn < column; inColumn++) {
            const column_row = isNumber(row) ? row : row[inColumn];
            for (let inRow = 0; inRow < column_row; inRow++) {
                cb(inColumn, inRow);
            }
        }
    }

    randomSymbols(symbols: number[]): number[][] {
        const ss = [];
        this.loopForReels((inColumn: number, inRow: number) => {
            let reel = ss[inColumn];
            const stmbol = randomInArr(symbols);
            if (!reel) {
                ss[inColumn] = [stmbol];
            } else {
                reel.push(stmbol);
            }
        });
        return ss;
    }

    getLoginUrl(): string {
        if (this.urlOptions.loginUrl) {
            return `${this.urlOptions.loginUrl}?url=${encodeURIComponent(QueryString.setParameter("fun", "0"))}`;
            // return this.urlOptions.loginUrl.replace('[%%REDIRECT%%]', encodeURIComponent(QueryString.setParameter("fun", "0")));
        } 
        return this.getLobbyUrl();
    }

    getLobbyUrl(): string {
        return this.urlOptions.lobbyUrl || '';
    }
}