import { LayoutContext } from "./layoutContext_3";

export class AssetsContext extends LayoutContext {
    private _isSupportAvif: boolean = false;
    private _isSupportWebp: boolean = false;

    get isSupportWebp() {
        return this._isSupportWebp;
    }

    get isSupportAvif() {
        return this._isSupportAvif;
    }  

    setSupportImagesFormat(isSupportWebp: boolean, isSupportAvif: boolean) {
        this._isSupportAvif = isSupportAvif;
        this._isSupportWebp = isSupportWebp;
    }

    filterImageFormat(formats: string[]): string {
        if (!this.isSupportAvif) formats.splice(formats.indexOf('avif'), 1);
        if (!this.isSupportWebp) formats.splice(formats.indexOf('webp'), 1);
        return formats[0];
    }
 }