import { useCallback, useContext, useEffect, useMemo, useRef, useState } from "react";
import { AppStateOptions, GameContext, GameContextOptions } from "../AppContext";
import { Arrange } from "../util/layout";
import { useTick } from "@pixi/react";
import { Ticker } from "pixi.js";
import { useApp } from "@pixi/react";
import { SlotContext } from "../model/context";
import { isNumber } from "../util/lang";

export function useThrottle(fn, delay, dep = []) {
    const { current } = useRef({ fn, timer: null });
    useEffect(function () {
        current.fn = fn;
    }, [fn]);

    return useCallback(function f(...args) {
        if (!current.timer) {
            current.timer = setTimeout(() => {
                clearTimeout(current.timer);
                delete current.timer;
            }, delay);
            current.fn(...args);
        }
    }, dep);
}


export function useDebounce(fn, delay, dep = []) {
    const { current } = useRef({ fn, timer: null });
    useEffect(function () {
        current.fn = fn;
    }, [fn]);

    return useCallback(function f(...args) {
        if (current.timer) {
            clearTimeout(current.timer);
        }
        current.timer = setTimeout(() => {
            current.fn(...args);
        }, delay);
    }, dep)
}

export function useOrientationChange(layout, deps: any[] = []) {
    const { state } = useContext(GameContext);
    const orientation = state.setting.orientation;
    return Arrange(layout, `${orientation}-`);
}

export function useMemoConstant<T>(v: T): T {
    return useMemo(() => {
        return v;
    }, [])
}

export function useMemoWith<T>(factory: () => T, dep: any[], t: boolean, defaultValue?: T): T {
    const { current } = useRef({ value: defaultValue });

    const value = useMemo(() => {
        if (t) {
            const value = factory();
            current.value = value;
            return value;
        } else {
            return current.value;
        }
    }, dep);

    return value;
}

export function useForceUpdateId(...dep: any[]) {
    const { current } = useRef({ forceUpdateId: 0 });

    const id = useMemo(() => {
        return current.forceUpdateId++;
    }, [...dep]);

    return id
}

export function useDelayFrameOnce(fn: () => void, time, on, dep: any[] = []) {
    const [ playing, setPlaying ] = useState(false)
    const [ startTime, setStartTime ] = useState(0);

    useEffect(() => {
        if (on) {
            setPlaying(true);
            setStartTime(SlotContext.Obj.tweenTime);
        }
    }, dep);

    useEffect(() => {
        if (on) {
            setPlaying(true);
            setStartTime(SlotContext.Obj.tweenTime);
        } else {
            setPlaying(false);
        }
    }, [on])

    useTick(() => {
        if (SlotContext.Obj.tweenTime - startTime > time) {
            fn();
            setPlaying(false);
        }
    }, playing);    
}

export function useFrame(time: number, fn: () => void, on: boolean, dep: any[] = []) {
    const { current } = useRef({ timer: null });

    useEffect(() => {
        if (on) {
            current.timer = 0;
        }
    }, dep);

    useEffect(() => {
        if (on) {
            current.timer = 0;
        }
    }, [on])

    useTick((delta, ticker) => {
        current.timer += ticker.elapsedMS;
        if (current.timer >= time) {
            current.timer = 0;
            fn();   
        }
    }, on); 
}

export function useGameContext<SPIN>() {
    return useContext(GameContext) as GameContextOptions<SPIN>
}