import { cloneElement } from "react";
import { Children } from "react";
import { utils } from 'pixi.js';

export function Arrange(layout, prefix: 'l-' | 'p-') {
    function setProperties(layout) {
        if (layout && layout.props) {
            const newProps = {}, props = layout.props;
            for (let key in props) {
                if (key.startsWith(prefix)) {
                    newProps[key.slice(2)] = props[key];
                }
            }
            if (layout.props.children) {
                newProps['children'] = Arrange(layout.props.children, prefix);
            }
            return cloneElement(layout, newProps);
        }
        return layout;
    }


    if (Array.isArray(layout)) {
        return Children.map(layout, (child) => {
           return setProperties(child); 
        })
    } else {
        return setProperties(layout);
    }
}

export function safeAreaInMobileL(defaultSafeArea: [number, number, number, number], enable: boolean): [number, number, number, number] {
    const [left, top, width, height] = defaultSafeArea;
    if (utils.isMobile.any && enable) {
        const windowWidth = document.documentElement.clientWidth, windowHeight = document.documentElement.clientHeight;
        const realWidth = Math.round(height / windowHeight * windowWidth);
        const widthGT = Math.round(Math.max(0, (realWidth - width) / 2));
        return [ left - widthGT ,top, Math.max(width, realWidth), height ];
    } 
    return defaultSafeArea;
}

export function safeAreaInMobileP(defaultSafeArea: [number, number, number, number], enable: boolean): [number, number, number, number] {
    const [left, top, width, height] = defaultSafeArea;
    if (utils.isMobile.any && enable) {
        const windowWidth = document.documentElement.clientWidth, windowHeight = document.documentElement.clientHeight;
        const heightGT = Math.max(0, (windowHeight - height) / 2);
        return [ left , top - heightGT , width, Math.max(height, windowHeight) ];
    } 
    return defaultSafeArea;
}